import { usersAPI, profileAPI } from '../api/api'

//action types
const ADD_POST_TO_STATE = 'ADD-POST-TO-STATE'
const SET_USER_PROFILE = 'SET_USER_PROFILE'
const SET_STATUS = 'SET_STATUS'

//   .     state  profileReducer   ,      
let initialState = {
  posts: [
    { id: 1, message: 'Hi, how are you?', likesCount: '15 likes' },
    { id: 2, message: "It's my first post", likesCount: '20 likes' },
  ],
  profile: null,
  status: '',
}

// state
const profileReducer = (state = initialState, action) => {
  //      
  switch (action.type) {
    case ADD_POST_TO_STATE: {
      //   
      return {
        ...state,
        //    ( ),       
        posts: [
          ...state.posts,
          //     
          {
            id: 5,
            message: action.payload.newPostText,
            likesCount: '0 likes',
          },
        ],
      }
    }
    //   SET_USER_PROFILE,             
    case SET_USER_PROFILE: {
      return { ...state, profile: action.profile }
    }
    case SET_STATUS: {
      return { ...state, status: action.status }
    }
    default:
      return state
  }
}

//ActionCreator -  -,   action: { type: SET_USER_PROFILE, profile }.
//action       ,    action      
//SET_USER_PROFILE    -   , profile -   
export const addPostActionCreator = (newPostText) => ({
  type: ADD_POST_TO_STATE,
  payload: {
    newPostText,
  },
})
export const setUserProfile = (profile) => ({ type: SET_USER_PROFILE, profile })
export const setStatus = (status) => ({ type: SET_STATUS, status })

//ThunkCreator
export const getUserProfile = (userId) => async (dispatch) => {
  const response = await usersAPI.getProfile(userId)
  //     (    response),  reducer   setUserProfile
  //    - setUserProfile    response   ,  
  dispatch(setUserProfile(response.data))
}

export const getStatus = (userId) => async (dispatch) => {
  const response = await profileAPI.getStatus(userId)
  dispatch(setStatus(response.data))
}

export const updateStatus = (status) => async (dispatch) => {
  const response = await profileAPI.updateStatus(status)
  //  (   1)   
  if (response.data.resultCode === 0) {
    dispatch(setStatus(status))
  }
}

export default profileReducer
