import { authAPI } from '../api/api'
const SET_USER_DATA = 'SET_USER_DATA'

//   .     action
let initialState = {
  userId: null,
  email: null,
  login: null,
  // 
  isAuth: false
}

// state     
const authReducer = (state = initialState, action) => {
  //    : action.currentPage, action.isFetching  
  switch (action.type) {
    case SET_USER_DATA:
      return {
        //      id, email, login       action.data
        ...state,
        // action  payload    id, email, login
        ...action.payload
      }
    default:
      return state
  }
}

//ActionCreator,     action,      reducer
export const setAuthUserData = (userId, email, login, isAuth) => ({
  type: SET_USER_DATA,
  payload: { userId, email, login, isAuth },
})

//ThunkCreator
export const getAuthUserData = () => async (dispatch) => {
    const response = await authAPI.me()
      //   resultCode = 0,     
      if (response.data.resultCode === 0) {
        const { id, email, login } = response.data.data
        //     AC  store
        dispatch(setAuthUserData(id, email, login, true))
      }
    }

  //thunk  
export const login = (email, password, rememberMe, setStatus) => async (dispatch) => {
// const isAuthenticated = selectors.isAuthenticated(getState())
  const response = await authAPI.login(email, password, rememberMe)
    //   resultCode = 0,           
    if (response.data.resultCode === 0) {
      dispatch(getAuthUserData())
    } else{
      setStatus({ error: response.data.messages })
    }
}

export const logout = () => async (dispatch) => {
  const response = await authAPI.logout()
    if (response.data.resultCode === 0) {
      dispatch(getAuthUserData(setAuthUserData(null, null, null, false)))
    }
}

export default authReducer
